@import url(https://fonts.googleapis.com/css?family=Quicksand:300&display=swap);
* {
  margin: 0;
  padding: 0; }

body {
  background: #f4efe8;
  color: #1d2a42;
  font-family: 'Quicksand', sans-serif;
  font-size: 18px;
  font-weight: 200; }

header {
  background: #1d2a42;
  color: #f4efe8;
  min-height: 160px;
  padding: 45px 0; }

.container {
  max-width: 930px;
  margin: 0 auto;
  padding: 0 15px; }

.flexbox {
  display: flex;
  flex-wrap: wrap; }

.divider {
  height: 5px;
  background: #d8a187; }

footer {
  padding: 15px 0; }

@media (max-width: 768px) {
  .flexbox {
    flex-direction: column; } }

div.header-logo {
  flex: 1 1; }
  div.header-logo img.header-logo-image {
    height: 160px;
    width: 160px; }

div.header-welcome {
  flex: 5 1;
  margin: 0 0 0 45px; }
  div.header-welcome h1.header-welcome-title {
    margin: 0 0 45px 0; }

@media (max-width: 768px) {
  div.header-logo {
    display: none; }
  div.header-welcome {
    margin: 0;
    text-align: center; } }

section#form {
  padding: 45px 0 0 0; }
  section#form .form-input {
    flex: 2 1;
    min-width: 200px; }
    section#form .form-input p.form-input-label {
      margin-bottom: 15px; }
    section#form .form-input .form-input-field {
      width: 100%;
      font-family: inherit;
      height: 30px;
      background: inherit;
      border: 1px #62778c solid; }
    section#form .form-input span.form-input-info {
      font-size: 14px;
      margin-top: 15px; }
  section#form #rover-selection {
    padding-right: 15px; }
  section#form #date-selection {
    padding: 0 15px; }
  section#form #camera-selection {
    padding-left: 15px; }
  section#form div#search-button {
    margin: 15px 0 45px 0; }
    section#form div#search-button button.form-submit-button {
      width: 100%;
      height: 30px;
      background: #c1542d;
      font-family: inherit;
      font-size: inherit;
      border: none;
      color: #f4efe8; }
  @media (max-width: 768px) {
    section#form .form-input {
      margin-bottom: 15px; }
    section#form #rover-selection,
    section#form #date-selection,
    section#form #camera-selection {
      padding: 0; } }

section#results {
  padding-top: 45px; }
  section#results h2.result-title {
    margin-bottom: 45px; }
  section#results ul.result-list li.result-list-item {
    border: 1px #62778c dashed;
    margin: 0 0 15px 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    min-height: 130px;
    padding: 15px 15px; }
    section#results ul.result-list li.result-list-item div.result-thumbnail {
      order: 1;
      flex: 1 1;
      height: 130px;
      width: 130px;
      margin-right: 15px; }
      section#results ul.result-list li.result-list-item div.result-thumbnail img.result-thumbnail-image {
        max-height: 100%;
        max-width: 100%; }
    section#results ul.result-list li.result-list-item div.result-description {
      order: 2;
      flex: 3 1; }
    section#results ul.result-list li.result-list-item div.result-view {
      order: 3;
      flex: 1 1;
      margin: 45px 0; }
      section#results ul.result-list li.result-list-item div.result-view button.result-view-button {
        width: 100%;
        height: 30px;
        background: #c1542d;
        font-family: inherit;
        font-size: inherit;
        border: none;
        color: #f4efe8; }
        section#results ul.result-list li.result-list-item div.result-view button.result-view-button a.result-view-button-link:link {
          text-decoration: none;
          color: #f4efe8; }
        section#results ul.result-list li.result-list-item div.result-view button.result-view-button a.result-view-button-link:visited {
          text-decoration: none;
          color: #f4efe8; }
  @media (max-width: 768px) {
    section#results ul.result-list li.result-list-item {
      flex-direction: column;
      text-align: center; }
      section#results ul.result-list li.result-list-item div.result-thumbnail {
        margin: 0 auto 15px auto; } }

div#footer {
  text-align: center;
  flex-direction: column; }
  div#footer div.footer-logo {
    order: 1; }
  div#footer div.footer-copyrights {
    order: 2; }
    div#footer div.footer-copyrights p.footer-copyrigths-text {
      font-size: 14px; }

